import { Redirect } from "react-router";
import { useGetProductQuery } from "../../generated/graphql";
import { useDispatch } from "react-redux";
import { addProductToCart } from "../../redux/actions/userAction";
import { useEffect } from "react";
import anime from "animejs";

const ProductDetails = () => {
    let product_id: number;
    if (process.env.NODE_ENV === "production") {
        product_id = Number(window.location.href.split(":")[2]);
    } else {
        product_id = Number(window.location.href.split(":")[3]);
    }

    const dispatch = useDispatch();

    const { data, loading, error } = useGetProductQuery({
        variables: {
            product_id,
        },
    });

    useEffect(() => {
        var elems = document.querySelectorAll(".tooltipped");
        M.Tooltip.init(elems);
        if (!loading) {
            // (stock * 100)/ org_stock
            let percent = 50;
            if (!data!.getProduct.org_stock) {
                percent = 100;
            } else {
                percent =
                    (data!.getProduct.stock * 100) / data!.getProduct.org_stock;
            }

            anime({
                targets: ".filler",
                width: ["0%", `${percent}%`],
                easing: "easeInOutExpo",
            });
        }
    });

    if (loading) {
        return <></>;
    }

    if (!data || error) {
        return <Redirect to="/products" />;
    }

    let product: any = data!.getProduct;

    console.log("product :>> ", product);
    return (
        <div>
            <h3 className="center-align">{product.name}</h3>
            <h6 className="center-align">
                ${Number(product.price / 100).toFixed(2)}
            </h6>

            <div className="row">
                <div
                    className="col s4 offset-s2"
                    style={{ display: "flex", justifyContent: "end" }}
                >
                    <div>
                        <img
                            src={product.images[0].img_url || ""}
                            alt="product"
                            style={{ maxWidth: "100%" }}
                        />
                    </div>
                </div>
                <div className="col s3 offset-s1">
                    <div style={{ marginBottom: "16px" }}>{product.desc}</div>
                    <div
                        className="divider"
                        style={{ marginBottom: "16px" }}
                    ></div>
                    <a
                        className="tooltipped"
                        data-position="right"
                        data-tooltip={`${product.stock} remaining`}
                    >
                        <div
                            style={{
                                height: "55px",
                                width: "100%",
                                border: "1px solid black",
                                marginBottom: "16px",
                            }}
                        >
                            <span
                                className="filler"
                                style={{
                                    display: "inline-block",
                                    height: "100%",
                                    backgroundColor: "black",
                                }}
                            ></span>
                        </div>
                    </a>

                    <button
                        style={{
                            width: "100%",
                            backgroundColor: "#000",
                            color: "#fff",
                            border: "none",
                        }}
                        onClick={() => {
                            dispatch(addProductToCart(product));
                        }}
                    >
                        Add to Cart
                    </button>
                </div>
            </div>
        </div>
    );
};

export default ProductDetails;
